import {
	ToolItemStyled,
	StyledLink,
	ToolLogo,
	ToolInfo,
	ToolTitle,
	ToolContent,
	ToolSettingsImg,
} from './ToolItem.styled';

export const ToolItem = ({ title, content, path, imgUrl }) => {
	return (
		<ToolItemStyled>
			<StyledLink to={`/${path}`}>
				<ToolLogo src={imgUrl} alt={`${title} logo`} />
				<ToolInfo>
					<ToolTitle>{title}</ToolTitle>
					<ToolContent>{content}</ToolContent>
				</ToolInfo>
				<ToolSettingsImg
					src="data:image/svg+xml;base64,"
					alt="Settings icon"
				/>
			</StyledLink>
		</ToolItemStyled>
	);
};
